<template>
	<div class="meta" >
	<div class="meta" v-bar ref="vbar">
	<div id="elements-conclusifs" class="view-scroll">
		<PageTitle/>
		<div class="center-col">
			<p>La réflexion menée sous le titre <em><i>Montréal/paysage en confinement</i></em> est fondée sur deux principaux intrants, soit une veille informationnelle et un relevé de photographies de presse et professionnelles.</p>
			<p>Le premier volet d’analyse a permis de discerner l’importance des enjeux reliés <router-link tag="a" class="reference-link" :to="{ name: 'Questionnements' }">aux modes de vie et à l’aménagement urbain</router-link>, de <router-link tag="a" class="reference-link" :to="{ name: 'AttributsUrbains' }">caractériser</router-link> et de <router-link tag="a" class="reference-link" :to="{ name: 'Initiatives' }">spatialiser</router-link> les préoccupations publiques et sociales envers l’espace public tout comme d’identifier les mots dominants utilisés pour <router-link tag="a" class="reference-link" :to="{ name: 'Humain' }">qualifier</router-link> la population montréalaise interpellée durant le confinement de la COVID-19. De plus, cette analyse révèle un intérêt très marqué pour l’espace public. La <i class="hl">rue</i> et le <i class="hl">parc public</i> constituent les deux principaux attributs du paysage urbain. Également, l’émergence de nouveaux attributs, soit le <i class="hl">corridor sanitaire</i> et les <i class="hl">voies actives sécuritaires</i>, a marqué l’aménagement de l’espace urbain montréalais. Ces attributs renvoient à la question de la mobilité montréalaise durant cette crise sanitaire où le ralentissement des flux automobiles, généré par le télétravail et la difficile gestion des transports en commun, a favorisé la «{{'\xa0'}}<i class="hl">mobilité douce</i>{{'\xa0'}}», renommée par les médias «{{'\xa0'}}mobilité lente{{'\xa0'}}», auprès des Montréalais. Le <i class="hl">vélo</i> et la <i class="hl">marche</i> ont rapidement gagné en popularité à Montréal et la municipalité semble avoir saisi cette crise sanitaire pour élargir rapidement son réseau de pistes cyclables. Par ailleurs, le qualificatif le plus souvent utilisé pour décrire l’«{{'\xa0'}}<i class="hl">Homo urbanus montréalais</i>{{'\xa0'}}» est le <i class="hl">piéton</i>. Ceci confirme l’intérêt accordé à la <i class="hl">mobilité douce</i> et surtout de l’importance de vivre l’espace public extérieur en cette période de crise sanitaire. La rue et le parc quartier s’avèrent constituer les antidotes du confinement montréalais confirmant ainsi l’enjeu du paysage de proximité. Sur d’autres considérations, la veille informationnelle a soulevé une grande préoccupation envers la population itinérante montréalaise et les problèmes de vie et d’accueil de celle-ci dans les espaces publics. Ces lieux semblent s’être adaptés avec la mise en place de <i class="hl">centres de jour</i>, de <i class="hl">centres d’hébergement d’urgence</i> et de <i class="hl">haltes-répit</i>.</p>
			<p>Le deuxième volet d’analyse concernait la <router-link tag="a" class="reference-link" :to="{name: 'LectureRegards'}">lecture paysagère</router-link> des regards photographiques, soit les cadrages visuels de l’espace public montréalais captés par des photographes. Les photographies urbaines à travers l’histoire ont majoritairement rapporté le caractère animé, chaleureux et actif des espaces publics. En période de confinement, l’animation urbaine est exempte des cadrages photographiques montréalais. La ville n’est plus la vie. L’urbain révèle un simple (et parfois majestueux) décor architectural et urbanistique sans acteur. Malgré la présence importante de la lumière, les photographes ont dépeint Montréal comme une ville <i class="hl">solitaire</i>, <i class="hl">froide</i> et <i class="hl">figée</i>. Cette lecture est d’autant marquée que le confinement montréalais prenait place dans un entre-deux saisonnier nordique et aride (hivers/printemps). Cependant, <router-link tag="a" class="reference-link" :to="{name: 'ExperiencePaysagere'}">l’interprétation</router-link> de ces cadrages photographiques a dévoilé des <i class="hl">expressions sublimantes</i>, ennoblies de la ville en mode confinement. Ainsi, la crise sanitaire de la COVID-19 a démontré la force et la singularité des architectures montréalaises (des passages et des façades de bâtiments) et des espaces souterrains sous la lumière crue des éclairages naturels et artificiels. Ce confinement a transcendé le «{{'\xa0'}}beau{{'\xa0'}}» dans la ville, soit les vues attendues, équilibrées par ces formes et rassurantes par ses ambiances urbaines connues de vie, de couleurs et de chaleur. Cette interprétation photographique a également rapporté les expressions et impressions paysagères où le regard sur l’espace de proximité est fragmentaire. Il est fermé sur lui-même et empreint de solitude.</p>
			<p>Pour conclure, cette réflexion a présenté un portrait global du paysage urbain montréalais en mode confinement avec les limites d’une analyse de veille informationnelle qui ne peut qu’être partielle face au vécu d’une population et d’une société en crise sanitaire. Il s’agit d’un aperçu. De plus, cette réflexion s’est confrontée aux contraintes du confinement du 13 mars au 16 mai 2020. L’observation directe sur le terrain (balisage photographique, entrevues, etc.) aurait sans nul doute enrichi certains constats.</p>
			<p>Cette réflexion aura toutefois souligné les principales préoccupations sociales et politiques d’une urbanité en confinement. Elle nous a livré des indices pour initier de nouvelles réflexions et actions sur la ville.</p>
			<p>Ainsi, elle amène à nous interroger sur l’empreinte du confinement urbain et la viabilité des mesures post-confinement de la COVID-19 dans les espaces publics montréalais qui semblent s’engager vers une ère post-hygiéniste. Comment peut-on favoriser et rendre accessible à tous, la nature et le grand air en ville{{'\xa0'}}? Comment doit-on réinventer l’espace de vie publique et privée (transitoire ou non){{'\xa0'}}; la rue, les trottoirs, les parcs, les aires de jeux, l’habitat domiciliaire, les commerces, les lieux de travail, les quartiers{{'\xa0'}}? Comment faut-il repenser le centre-ville pour en conserver sa vitalité sociale, culturelle et économique{{'\xa0'}}? Comment réaménage-t-on les lieux de rencontre et de rassemblement pour sauvegarder la culture en ville{{'\xa0'}}? Quels seront les contours d’une convivialité urbaine soucieuse des uns et autres{{'\xa0'}}? Et plus largement, comment va-t-on repenser le devenir durable des villes face aux défis convergents (sanitaires, environnementaux, sociaux, etc.) auxquels elles sont confrontées en ce début de siècle{{'\xa0'}}?</p>
			<p>Les contours des lendemains du confinement de la COVID-19 restent encore très flous malgré l’engagement motivé des pouvoirs publics et la créativité des architectes et designers urbains pour adapter la ville et proposer des lieux de vie transitoires sains et sécuritaires à Montréal comme ailleurs. Toutefois et tel qu’en témoignent les nombreux débats et forums internationaux sur le post-confinement (printemps/été 2020), les questionnements sur l’avenir des villes après cette crise sanitaire mondiale sont encore en quête de réponses et exigent un véritable recul. Une mise à distance réflexive s’impose de la part des acteurs territoriaux, du public et des experts de la ville.</p>
			
